<template>
  <div class="settings-page">
    <section class="card">
      <h3 class="section-title">基础信息</h3>
      <van-field
        v-model="localNickname"
        label="昵称"
        placeholder="输入昵称"
        maxlength="12"
        @blur="updateNickname"
      />
      <van-field label="主题">
        <template #input>
          <van-radio-group
            v-model="localTheme"
            direction="horizontal"
            @change="value => updateSetting({ key: 'theme', value })"
          >
            <van-radio name="light">浅色</van-radio>
            <van-radio name="dark">深色</van-radio>
          </van-radio-group>
        </template>
      </van-field>
    </section>

    <section class="card mt-16">
      <h3 class="section-title">学习偏好</h3>
      <van-switch-cell
        v-model="localAutoExplain"
        title="自动展示解析"
        @change="value => updateSetting({ key: 'autoShowExplanation', value })"
      />
      <van-switch-cell
        v-model="localHint"
        title="启用答题提示"
        @change="value => updateSetting({ key: 'enableHint', value })"
      />
    </section>

    <section class="card mt-16">
      <h3 class="section-title">数据管理</h3>
      <van-button type="danger" block plain @click="handleClear">
        清除本地数据
      </van-button>
    </section>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import { clearState } from '@/services/storageService';

export default {
  name: 'SettingsPage',
  data() {
    return {
      localNickname: '',
      localTheme: 'light',
      localAutoExplain: true,
      localHint: false,
    };
  },
  computed: {
    ...mapState('user', ['nickname', 'theme', 'autoShowExplanation', 'enableHint']),
  },
  watch: {
    nickname: {
      immediate: true,
      handler(value) {
        this.localNickname = value;
      },
    },
    theme: {
      immediate: true,
      handler(value) {
        this.localTheme = value;
      },
    },
    autoShowExplanation: {
      immediate: true,
      handler(value) {
        this.localAutoExplain = value;
      },
    },
    enableHint: {
      immediate: true,
      handler(value) {
        this.localHint = value;
      },
    },
  },
  methods: {
    ...mapActions('user', ['updateSetting']),
    updateNickname() {
      const value = this.localNickname.trim() || '学习英雄';
      this.updateSetting({ key: 'nickname', value });
    },
    async handleClear() {
      try {
        await this.$dialog.confirm({
          title: '确认操作',
          message: '将删除本地题库、进度与设置，是否继续？',
        });
      } catch {
        return;
      }
      clearState();
      this.$store.commit('questions/RESET_STATE');
      this.$store.commit('progress/RESET_PROGRESS', { levels: [] });
      this.$store.commit('user/HYDRATE');
      this.$toast.success('已清除本地数据');
    },
  },
};
</script>

<style lang="scss" scoped>
.settings-page {
  max-width: 640px;
  margin: 0 auto;
}
</style>


